<template>
  <div>
    <van-nav-bar title="结算" left-arrow left-text="返回" @click-left="$router.back()"></van-nav-bar>
    <div class="list">
      <van-cell-group class="item-gap">
        <van-cell :title="address.title" :label="address.label" is-link @click="$router.push({name:'addressList'})" />
      </van-cell-group>
      <van-cell-group class="item-gap">
        <van-cell v-for="product in orderProductList" :key="product.id" :title="product.name"
          :value="`${product.count}件`" :label="`￥${product.price}`" :icon="product.cover" />
      </van-cell-group>
    </div>
    <van-submit-bar :price="totalPrice" :loading="$store.isLoading" :disabled="productCounts === 0" button-text="付款"
      @submit="pay">共{{productCounts}}件
    </van-submit-bar>
    <van-popup class="van-popup-pay" v-model="showPayPassword" position="bottom" :close-on-click-overlay="false">
      <van-nav-bar class="pay-nav-bar" title="付款" @click-left="cancelPay">
        <van-icon slot="left" name="cross" />
      </van-nav-bar>
      <van-password-input :value="payPassword" info="密码为 6 位数字" />
      <van-number-keyboard :show="showPayPassword" @input="onInput" @delete="onDelete" />
    </van-popup>
  </div>
</template>

<script>
  export default {
    data() {
      let buyProducts = []
      let orderProductList = []
      if(sessionStorage.buyProducts){
        orderProductList = JSON.parse(sessionStorage.buyProducts)
      }else{
        this.$Toast('加载购买商品信息错误')
      }
      return {
        showPayPassword: false,
        payPassword: '',
        orderId: -1,
        orderProductList
      }
    },
    watch: {
      payPassword: {
        handler(val) {
          if (val.length >= 6) {
            this.$axios.post(`/order/pay?order_id=${this.orderId}`, {
              pay_password: val
            }).then(res => {
              this.payPassword = ''
              if (res.errcode === 0) {
                this.$router.push({
                  name: 'paySuccess',
                  query: { orderId: this.orderId }
                })
              } else if (res.errcode === 901201) {
                this.$Toast(res.errmsg)
              } else {
                this.$router.push({
                  name: 'payFail',
                  query: { orderId: this.orderId }
                })
              }
            })
          }
        }
      }
    },
    computed: {
      address() {
        return this.$store.chosenAddress.id ? {
          title: `${this.$store.chosenAddress.name}, ${this.$store.chosenAddress.mobile}`,
          label: `${this.$store.chosenAddress.province} ${this.$store.chosenAddress.city} ${this.$store.chosenAddress.country} ${this.$store.chosenAddress.detail}`
        } : {
            title: '请选择收货地址',
            label: ''
          }
      },
      totalPrice() {
        let t = 0
        return this.orderProductList.reduce((prev, item) => {
          return t += item.price * item.count
        }, t) * 100
      },
      cart() {
        return this.$store.cart
      },
      productCounts() {
        let t = 0
        return this.orderProductList.reduce((prev, curr, index) => {
          return t += curr.count
        }, t)
      },
      addressList() {
        return this.$store.addressList
      }
    },
    mounted() {

    },
    methods: {
      cancelPay() {
        this.showPayPassword = false
        this.$router.push({ name: 'order', params: { id: this.orderId } })
      },
      pay() {
        if (!this.$store.chosenAddress.id) {
          this.$Toast('请填写收货地址')
        } else {
          this.$axios.post('/order/create', {
            orderProductList: this.orderProductList,
            user_address_id: this.$store.chosenAddress.id
          }).then(res => {
            if (res.errcode === 0) {
              this.orderId = res.data.id
              this.showPayPassword = true
              for (let i = 0; i < this.$store.cart.length; i++) {
                if (this.$store.cart[i].checked) {
                  this.$store.cart.splice(i, 1)
                  i--
                }
              }
            } else {
              this.$Toast(res.errmsg)
            }
          })
        }
      },
      onInput(key) {
        this.payPassword = (this.payPassword + key).slice(0, 6);
      },
      onDelete() {
        this.payPassword = this.payPassword.slice(0, this.payPassword.length - 1);
      }
    },
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .van-submit-bar {
    padding-left: 1em;
  }
</style>


